<template>
  <div class="py-6">
    <v-container>
      <v-row
        v-for="(s, i) in splitItems"
        :key="i"
      >
        <v-col cols="12" class="font-weight-bold">
          {{ s[0].title }} <span class="text-caption">( {{ s[0].date }} )</span>
  			</v-col>
        <v-col
          v-for="(item, index) in s"
          :key="index"
          cols="3"
          class="pa-1"
          @click="openDialog(item.image)"
        >
          <v-img
            :src="item.image"
            aspect-ratio="1"
          />
  			</v-col>
      </v-row>
    </v-container>
    
    <v-dialog
      v-model="dialog"
    >
      <v-card>
        <div class="pa-2 d-flex justify-space-between">
          <v-btn
            icon
          	color="blue darken-3"
            @click="closeDialog"
          >
            <v-icon>mdi-close</v-icon>
  				</v-btn>
          <v-btn
            icon
          	color="blue darken-3"
            @click="download"
          >
            <v-icon>mdi-download</v-icon>
  				</v-btn>
  			</div>
        <v-img
          max-height="100%"
          max-width="100%" 
          :src="dialogImage"
        />
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    name: 'VerticalPhoto',
    props: [
      'items',
      'loading'
    ],
    data: () => ({
      dialog: false,
      dialogImage: '',
    }),
    computed: {
      splitItems() {
        const newItems = [];
        const chunks = new Set(this.items.map(d => d.chunk));
        chunks.forEach(c => {
          newItems.push(this.items.filter(d => d.chunk === c));
        });
        return newItems;
      }
    },
    methods: {
      openDialog(item) {
        this.dialogImage = item;
        this.dialog = true;
      },
      closeDialog() {
        this.dialogImage = '';
        this.dialog = false;
      },
      download() {
        const a = document.createElement('a');
        a.href = this.dialogImage;
        a.download = 'downloaded';
        a.target = '_blank';
        a.click();
      }
    }
  }
</script>