<template>
  <div>
    <v-tabs
      v-model="selected"
    	fixed-tabs
      background-color="white"
    	@change="tabChanged"
    >
      <v-tab
        v-for="(tab, index) in tabs"
        :key="index"
      >{{ tab.text }}</v-tab>
  	</v-tabs>
    <div style="border-bottom: 1px solid lightgrey"/>
    
    <v-tabs-items 
      v-model="selected" 
    	style="background-color: #f6f6f6; min-height: calc(100vh - 105px);"
    	@change="tabChanged"
    >
  		<v-tab-item
        v-for="(tab, index) in tabs"
        :key="index"
      >
        <Vertical-Photo
          v-if="tabs[selected].value === 'photos'"
          :items="tabs[selected].items"
        />
        <Vertical-Card
          v-else
          :items="tabs[selected].items"
          :loading="loading"
          
        />
        <div class="text-center pb-6">
          <v-btn 
            rounded
            dark
            color="blue darken-3"
            class="ma-auto"
            :loading="loading"
            @click="loadMore()"
          >더보기</v-btn>
        </div>
  		</v-tab-item>
  	</v-tabs-items>
  </div>
</template>

<script>
import VerticalCard from '../components/VerticalCard';
import VerticalPhoto from '../components/VerticalPhoto';

export default {
  name: 'ContentsView',
  components: {
   	VerticalCard,
    VerticalPhoto,
  },
  data: () => ({
    loading: false,
    selected: 0,
    chunk: 0,
    tabs: [
      {
       	text: '공지사항',
        value: 'notices',
        items: [],
      },
      {
       	text: '소식',
        value: 'news',
        items: [],
      },
      {
       	text: '도서관',
        value: 'books',
        items: [],
      },
      {
       	text: '갤러리',
        value: 'photos',
        items: [],
      }
    ]
  }),
  async created () {
    if (this.$route.hash) {
      this.selected = this.tabs.findIndex(t => '#'+t.value === this.$route.hash);
    }
    this.tabChanged();
  },
  methods: {
    async tabChanged () {
      if ('#'+this.tabs[this.selected].value !== this.$route.hash) {
        this.$router.replace(`${this.$route.path}#${this.tabs[this.selected].value}`);
      }
      this.loading = true;
      this.chunk = 0;
      this.tabs[this.selected].items = await this.$store.dispatch('getData', { 
        type: this.tabs[this.selected].value, 
        chunk: this.chunk
      });
      this.loading = false;
    },
    async loadMore() {
      this.loading = true;
      const nextChunk = await this.$store.dispatch('getData', { 
        type: this.tabs[this.selected].value, 
        chunk: this.chunk + 1
      });
      if (nextChunk.length !== 0) {
        nextChunk.forEach(i => this.tabs[this.selected].items.push(i));
        this.chunk++;
      }
      this.loading = false;
    }
  }
}
</script>