<template>
  <div>
    <v-app-bar
      flat
      outlined
      elevation="1"
      color="white"
    >
      <div class="d-flex align-center">
        <v-icon @click="clear">mdi-arrow-left</v-icon>
      </div>
      <v-text-field
        v-model="searchText"
        v-on:keyup.enter="search"
        @click:append="search"
        rounded
        flat
        solo
        dense
        hide-details
        color="grey"
      	background-color="grey lighten-4"
        label="검색어를 입력해주세요"
        append-icon="mdi-magnify"
        class="ma-2"
        :loading="loading"
      />
    </v-app-bar>
    
    <section style="background-color: white;">
  		<v-container>
  			<v-row v-if="items.length === 0">
  				<v-col cols="12" class="font-weight-bold pb-0">
  					추천 검색어
  				</v-col>
          <v-col cols="12" class="text-caption py-0">
  					궁금한 콘텐츠를 검색해 보세요.
  				</v-col>
          <v-col cols="12">
  					<v-chip
            	v-for="(item, index) in recommended"
              :key="index"
              small
              outlined
              class="mr-2 mb-2"
              @click="clickChip(item)"
            >{{ item }}</v-chip>
  				</v-col>
  			</v-row>
        <v-row v-else>
          <v-col cols="12" class="pa-0">
            <Vertical-List
              :items="items"
            />
          </v-col>
  			</v-row>
  		</v-container>
  	</section>
  </div>
</template>

<script>
import VerticalList from '../components/VerticalList';
  
export default {
  name: 'SearchView',
  components: {
  	VerticalList,
  },
  data: () => ({
    loading: false,
    searchText: '',
    items: [],
    recommended: [
      '기후',
      '여성',
      '인문',
      '청년',
      '청년기후포럼',
      '피스오브피스',
    ]
  }),
  methods: {
    async search() {
      this.loading = true;
      this.items = await this.$store.dispatch('search', { type: 'search', search: this.searchText });
      this.loading = false;
    },
    clickChip(text) {
      this.searchText = text;
      this.search()
    },
    clear() {
      if (this.items.length > 0) {
        this.searchText = '';
        this.items = [];
      } else {
       	this.$router.go(-1); 
      }
    }
  }
};
</script>
