<template>
  <div>
    <v-app-bar
      flat
      outlined
      elevation="1"
      color="white"
    >
      <div class="d-flex align-center">
        <v-icon @click="$router.go(-1)">mdi-arrow-left</v-icon>
      </div>
      <v-spacer/>
      <div class="text-h6">
        상세보기
  		</div>
      <v-spacer/>
      <div class="d-flex align-center">
        <v-icon @click="$router.push('/')">mdi-home-outline</v-icon>
      </div>
    </v-app-bar>
    
    <section class="pt-0 pb-8 bg">
      <v-container>
        <v-row class="justify-center">
          <v-col v-if="loading" cols="12">
            <v-skeleton-loader type="image, article"></v-skeleton-loader>
          </v-col>
          <v-col v-else cols="12" class="pt-0">
            <div 
              v-html="docHtml"
              class="doc"
            ></div>
          </v-col>
        </v-row>
      </v-container>
    </section>
  </div>
</template>

<script>
  import axios from 'axios';
  
  export default {
    name: 'PageView',
    data: () => ({
      loading: false,
      content: {},
      docHtml: '',
    }),
    created() {
      this.init();
    },  
    watch: {
      $route(to, from) {
        this.init();
      }
    },
    methods: {
      async init() {
        this.loading = true;
        this.docHtml = await this.getDocument(this.$route.params.id);
        this.loading = false;
      },
      getDocument(id) {
        return new Promise((resolve, reject) => {
          axios({
            url: `https://docs.google.com/feeds/download/documents/export/Export?id=${id}&exportFormat=html`,
            method: 'get'
          })
          .then(res => resolve(res.data))
          .catch(err => reject(err));
        });
      }
    }
  };
</script>

